export var vkGetParam = function (param) {
    var canBeNull = param === "group_id" || param === "viewer_group_role" || param === "ts";
    var vk_param = "vk_" + param;
    var param_value = new URL(window.location.toString()).searchParams.get(vk_param);
    if (!canBeNull && !param_value)
        throw new TypeError("Launched app doesn't contain " + vk_param + " in url. Make sure that you launch app from VK.");
    if (param === "user_id" ||
        param === "app_id" ||
        param === "ts") {
        return +param_value;
    }
    else if (param === "is_app_user" ||
        param === "are_notifications_enabled" ||
        param === "is_favorite") {
        return Boolean(+param_value);
    }
    else {
        return param_value;
    }
};
export var vkIsDesktopVersion = function () { return vkGetParam("platform") === "desktop_web"; };
